.App {
	font-family: 'Poppins', sans-serif;
	max-width: 1200px;
	background-color: rgb(255, 255, 255);
}

/* Navbar Component */
nav {
	display: flex;
	padding: 1.7em 2.4em;
	box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);
}

.nav-logo {
	height: 4vh;
}

/* Navbar Component */

/* Hero Component */
section {
	padding: 20px;
}

.hero {
	display: flex;
	flex-direction: column;
}

.hero-photo {
	max-width: 95vw;
	align-self: center;
}

.hero-header {
	margin-bottom: 0.7em;
	font-size: 2.2em;
	line-height: 1.2em;
}

.hero-text {
	margin-top: 0;
}

/* Hero Component */

/* Card Component */
.cards-list {
	display: flex;
	flex-wrap: nowrap;
	gap: 20px;
	overflow-x: auto;
}

.card {
	width: 47vw;
	max-width: 175px;
	font-size: 0.79em;
	flex: 0 0 auto;
	display: flex;
	flex-direction: column;
	position: relative;
}

.card-image {
	width: 100%;
	border-radius: 9px;
	margin-bottom: 9px;
}

.card:nth-of-type(4) {
	filter: sepia(50%);
}

.card:nth-of-type(5) {
	filter: saturate(30%);
}

.card:nth-of-type(6) {
	filter: contrast(135%) brightness(120%);
}


.card-title {
	overflow: hidden;
	text-overflow: ellipsis;
}

.card-stats {
	display: flex;
	align-items: center;
}

.card-star {
	height: 1.3em;
}

.bold {
	font-weight: bold;
}

.grey {
	color: #918E9B;
}

.card-price {
	margin-top: auto;
}

.card-badge {
	position: absolute;
	top: 6px;
	left: 6px;
	background-color: white;
	padding: 5px 7px;
	border-radius: 2px;
	font-weight: bold;
}

/* Card Component */

/* Footer Component */
footer {
	text-align: center;
	font-size: 1em;
	color: #DEEBF8;
	background-color: #38363e;
}

footer p {
	padding: 0.7em 0;
	margin: 0;
}

a {
	text-decoration: none;
	color: #DEEBF8;
}

a:hover {
	filter: opacity(40%);
}

/* Footer Component */